*,
*::before,
*::after {
  box-sizing: border-box;
}
@font-face {
  font-family: "Pt Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/ptsans.woff") format("woff"), url("../../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Pt Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/ptsansbold.woff") format("woff"), url("../../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.main-nav {
  color: #ffffff;
  font-size: 18px;
  line-height: 26px;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__list-item {
  margin: 0;
  padding: 20px;
  padding-top: 17px;
  padding-bottom: 12px;
  font-weight: 700;
  background-color: #81b3d2;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
  text-align: center;
}
.main-nav__list-item:first-child {
  border-top: none;
}
.main-nav__list-item:last-child {
  position: relative;
  border-bottom: none;
}
.main-nav__list-item:last-child::after {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  height: 7px;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.2));
}
.main-nav__link {
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 768px;
    margin: 0 auto;
  }
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 29px;
  }
  .main-nav__list-item {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: #ffffff;
    border: none;
  }
  .main-nav__list-item--current {
    color: #766357;
  }
  .main-nav__list-item:last-child::after {
    content: none;
  }
}
.header {
  position: relative;
}
.header__logo-small {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 185px;
  height: 96px;
}
.advantages {
  text-align: center;
}
.advantages__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.advantages__item--wide {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 700;
  color: #ffffff;
  background-color: #81b3d2;
}
.advantages__item--with-background {
  background-color: #eeeeee;
}
.advantages__item-picture {
  width: 100%;
  height: 260px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 256px;
  }
  .advantages__item--wide {
    display: grid;
    grid-template-columns: repeat(3, minmax(256px, 1fr));
    grid-column: span 3;
  }
  .advantages__item--wide-reverse .advantages__item-picture {
    order: -1;
  }
  .advantages__item-picture {
    grid-column: span 2;
    object-fit: cover;
    width: 100%;
    height: 256px;
  }
  .advantages__item-picture--scale-position {
    object-position: top calc(27px - 3.4vw) right 0;
  }
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(256px, 1fr));
  }
}
.button {
  display: block;
  padding: 30px;
  padding-top: 14px;
  padding-bottom: 17px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
}
.feedback {
  text-transform: uppercase;
}
.feedback__title {
  width: 280px;
  margin: 0 auto;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}
.feedback__form-button {
  width: 100%;
}
.feedback__button-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 280px;
  margin: 0 auto;
}
.form-group {
  width: 280px;
  margin: 0 auto;
  border: none;
}
.form-group__title {
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.form-group__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.form-group__field {
  display: block;
  width: 100%;
  height: 58px;
  font-size: inherit;
  line-height: inherit;
  font-weight: 700;
  background-color: #e7e5e5;
  border: none;
}
.form-group__field::placeholder {
  font-size: 18px;
  line-height: 26px;
}
.gallery {
  width: 280px;
  margin: 0 auto;
}
.gallery__description {
  text-align: center;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
}
.gallery__card-description {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
}
.gallery__card-list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.logo-big {
  min-height: 389px;
  padding-top: 90px;
  text-align: center;
  background-color: #d8e6f1;
  background-image: url("../../img/intro-mobile.jpg");
  background-position: 50% 0;
  background-repeat: no-repeat;
}
.logo-big--inner {
  min-height: 160px;
  padding-top: 52px;
  background-position: 50% 100%;
}
@media (min-width: 768px) {
  .logo-big {
    padding-top: 70px;
    background-image: url("../../img/intro-tablet-desktop.jpg");
    background-position: 50% -3px;
  }
  .logo-big--inner {
    min-height: 290px;
    padding-top: 95px;
    background-position: 50% 100%;
  }
  .logo-big--inner .logo-big__img {
    width: 457px;
    height: 78px;
  }
  .logo-big__img {
    width: 461px;
    height: 360px;
  }
}
@media (min-width: 1200px) {
  .logo-big--inner .logo-big__img {
    width: 460px;
    height: 80px;
  }
}
@media (min-resolution: 2dppx) {
  .logo-big {
    background-image: url("../../img/intro-mobile@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-resolution: 2dppx) and (min-width: 768px) {
  .logo-big {
    background-image: url("../../img/intro-tablet-desktop@2x.jpg");
    background-size: 1200px auto;
  }
}
.main-container {
  flex-grow: 1;
}
.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  min-height: 233px;
  margin: 0 auto;
  width: 284px;
  text-align: center;
}
.page-footer__social {
  margin: 0;
}
.page-footer__hashtag {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    min-height: 120px;
  }
}
.page-title {
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
}
.page {
  height: 100%;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "Pt Sans", sans-serif;
  font-size: 14px;
  line-height: 21px;
  background-color: #f2f2f2;
}
.page__body-container {
  max-width: 1200px;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .page__body-container {
    margin: 0 auto;
  }
}
.promo {
  text-align: center;
}
.promo__title {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}
.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 156px;
  padding: 0;
  list-style: none;
}
.social__link {
  display: block;
  width: 48px;
  height: 48px;
  background-color: #81b3d2;
}
.video {
  width: 280px;
  margin: 0 auto;
  text-align: center;
}
.video__title {
  font-size: 18px;
  line-height: 26px;
}
.video__description {
  padding: 0 2px;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}
.wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 284px;
}
@media (min-width: 768px) {
  .wrapper {
    width: 520px;
  }
}
